.note {
  display: flex;
  margin: 10px 0;
  border: 1px solid #000;
}

.note .text {
  flex: 1;
  padding: 10px;
  margin: 0;
  border-right: 1px solid #000;
  font-size: 20px;
  line-height: 30px;
}

.note .button {
  display: block;
  width: 100px;
  padding: 20px 10px;
  border: none;
}